.container-todo{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #67C6E3;
}

.container-cuadro{
  display: flex;
  flex-direction: column;
  padding: 16px 24px;
  border: 2px solid black;
  box-shadow: 4px 6px 2px 2px;
  background-color: #B7C9F2;
  border-radius: 12px;
}

.question{
  text-align: center;
}

.button-click{
  background-color: transparent;
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 8px;
}